import React, {Component} from 'react';
import connect from '../store/connect';

import {Tabs} from 'antd';
import img from '../assets/lun-1.jpg'

const TabPane = Tabs.TabPane;

class Page extends Component {
    constructor(props) {
        super(props);
        this.state = {};
    }

    componentWillMount() {}

    render() {
        return (
            <div style={{width: '80%', margin: '0 auto'}}>
                <img src={img} style={styles.img} alt=''/>
                <div style={{margin: '.2rem auto'}}><h2></h2></div>
                <Tabs
                    defaultActiveKey="1"
                    tabPosition='left'
                    style={{ height: 420 }}>
                    <TabPane tab="" key="1">Content of tab 1</TabPane>
                    <TabPane tab="" key="2">Content of tab 2</TabPane>
                    <TabPane tab="" key="3">Content of tab 3</TabPane>
                    <TabPane tab="" key="4">Content of tab 4</TabPane>
                    <TabPane tab="" key="5">Content of tab 5</TabPane>
                    <TabPane tab="" key="6">Content of tab 6</TabPane>
                    <TabPane tab="" key="7">Content of tab 7</TabPane>
                </Tabs>
            </div>
        );
    }
};

const styles = {
    img: {
        width: '100%'
    }
}
export default connect(Page);


